import {
  RedPacket,
  RedPacketRainConfig,
  RedPacketClickEvent,
  AnimationState,
  PerformanceStats,
  RedPacketEventCallback,
  AnimationStateCallback,
  PerformanceCallback,
  Particle,
  ParticleConfig,
} from "./types";

// ÈªòËÆ§ÈÖçÁΩÆ
const DEFAULT_CONFIG: RedPacketRainConfig = {
  containerWidth: "100%",
  containerHeight: "100%",
  count: 20, // üéØ ËÆæÁΩÆÂêàÁêÜÁöÑÈªòËÆ§ÂÄº
  density: 5,
  speed: {
    min: 2,
    max: 6,
  },
  size: {
    width: 60,
    height: 80,
  },
  rotation: true,
  rotationSpeed: {
    min: -0.02,
    max: 0.02,
  },
  enablePerformanceMode: true,
  maxFPS: 60,
  qualityMaxFPS: 144,
  clickEffect: true,
  isParticle: true,
};

export class RedPacketRainEngine {
  private canvas: HTMLCanvasElement;
  private ctx: CanvasRenderingContext2D;
  private config: RedPacketRainConfig;
  private redPackets: RedPacket[] = [];
  private animationId: number | null = null;
  private isRunning = false;
  private isPaused = false;
  private lastFrameTime = 0;
  private frameCount = 0;
  private lastFpsTime = 0;
  private currentFPS = 0;
  private lastSpawnTime = 0;

  // ÂÆûÈôÖÂ∞∫ÂØ∏
  private actualWidth = 800;
  private actualHeight = 600;

  // ÂØπË±°Ê±†
  private redPacketPool: RedPacket[] = [];
  private poolSize = 50;
  private redPacketDataIndex = 0;

  // ‰∫ã‰ª∂ÂõûË∞É
  private onRedPacketClickCallback?: RedPacketEventCallback;
  private onAnimationStateChangeCallback?: AnimationStateCallback;
  private onPerformanceUpdateCallback?: PerformanceCallback;

  // ÁÆÄÂåñÁöÑÁªüËÆ°
  private stats = {
    totalCollected: 0,
    totalValue: 0,
    fps: 0,
  };

  private redPacketImage: HTMLImageElement | null = null;

  // üéØ Èò≤ÊäñÂíåË∞ÉËØïÁõ∏ÂÖ≥
  private lastClickTime = 0;
  private clickDebounceDelay = 100; // 100msÈò≤Êäñ
  private debugClicks: Array<{ x: number; y: number; time: number; hit: boolean }> = [];

  // üéÜ Á≤íÂ≠êÁ≥ªÁªü
  private particles: Particle[] = [];
  private particlePool: Particle[] = [];
  private particlePoolSize = 100;
  private particleConfig: ParticleConfig = {
    count: 15,
    speed: { min: 2, max: 8 },
    size: { min: 3, max: 8 },
    life: { min: 800, max: 1500 },
    colors: ["#FF6B6B"],
    gravity: 0.3,
    friction: 0.98,
    spread: Math.PI * 2, // 360Â∫¶Êï£Â∏É
  };

  constructor(canvas: HTMLCanvasElement, config?: Partial<RedPacketRainConfig>) {
    this.canvas = canvas;
    this.ctx = canvas.getContext("2d")!;
    this.config = { ...DEFAULT_CONFIG, ...config };

    // üéØ ‰ºòÂåñÈÖçÁΩÆÂ§ÑÁêÜÈÄªËæë
    this.optimizeConfig();

    this.resizeCanvas();
    this.initializePool();
    this.initializeParticlePool();
    this.bindEvents();
    this.loadRedPacketImage();
  }

  private initializePool(): void {
    for (let i = 0; i < this.poolSize; i++) {
      this.redPacketPool.push(this.createRedPacket());
    }
  }

  // üéÜ ÂàùÂßãÂåñÁ≤íÂ≠êÂØπË±°Ê±†
  private initializeParticlePool(): void {
    for (let i = 0; i < this.particlePoolSize; i++) {
      this.particlePool.push(this.createParticle());
    }
  }

  // üéÜ ÂàõÂª∫Á≤íÂ≠êÂØπË±°
  private createParticle(): Particle {
    return {
      id: Math.random().toString(36).slice(2, 11),
      x: 0,
      y: 0,
      vx: 0,
      vy: 0,
      size: 5,
      opacity: 1,
      color: "#FFD700",
      life: 1,
      maxLife: 1000,
      gravity: 0.3,
      friction: 0.98,
      rotation: 0,
      rotationSpeed: 0,
    };
  }

  // üéÜ ‰ªéÂØπË±°Ê±†Ëé∑ÂèñÁ≤íÂ≠ê
  private getParticleFromPool(): Particle {
    return this.particlePool.pop() || this.createParticle();
  }

  // üéÜ ËøîËøòÁ≤íÂ≠êÂà∞ÂØπË±°Ê±†
  private returnParticleToPool(particle: Particle): void {
    if (this.particlePool.length < this.particlePoolSize) {
      this.particlePool.push(particle);
    }
  }

  // üéÜ ÈáçÁΩÆÁ≤íÂ≠êÂ±ûÊÄß
  private resetParticle(particle: Particle, x: number, y: number): void {
    const config = this.particleConfig;
    const angle = Math.random() * config.spread;
    const speed = config.speed.min + Math.random() * (config.speed.max - config.speed.min);

    particle.x = x;
    particle.y = y;
    particle.vx = Math.cos(angle) * speed;
    particle.vy = Math.sin(angle) * speed - Math.random() * 2; // Âêë‰∏äÁöÑÂàùÂßãÈÄüÂ∫¶
    particle.size = config.size.min + Math.random() * (config.size.max - config.size.min);
    particle.initialSize = undefined; // Ê∏ÖÈô§ÂàùÂßãÂ§ßÂ∞èÔºåËÆ©updateParticlesÈáçÊñ∞ËÆæÁΩÆ
    particle.opacity = 1;
    particle.color = config.colors[Math.floor(Math.random() * config.colors.length)];
    particle.maxLife = config.life.min + Math.random() * (config.life.max - config.life.min);
    particle.life = particle.maxLife;
    particle.gravity = config.gravity;
    particle.friction = config.friction;
    particle.rotation = Math.random() * Math.PI * 2;
    particle.rotationSpeed = (Math.random() - 0.5) * 0.2;
  }

  private getRedPacketFromPool(): RedPacket {
    const redPacket = this.redPacketPool.pop() || this.createRedPacket();
    this.resetRedPacket(redPacket);
    return redPacket;
  }

  private returnRedPacketToPool(redPacket: RedPacket): void {
    if (this.redPacketPool.length < this.poolSize) {
      this.redPacketPool.push(redPacket);
    }
  }

  private getRedPacketData(): any | null {
    if (!this.config.redPackets || typeof this.config.redPackets === "number") {
      return null;
    }
    if (Array.isArray(this.config.redPackets) && this.config.redPackets.length > 0) {
      const data = this.config.redPackets[this.redPacketDataIndex];
      this.redPacketDataIndex = (this.redPacketDataIndex + 1) % this.config.redPackets.length;
      return data;
    }
    return null;
  }

  private isSpecialRedPacket(item: any): boolean {
    if (!this.config.isSpecialFn) return false;
    if (typeof this.config.isSpecialFn === "function") {
      return this.config.isSpecialFn(item);
    }
    if (typeof this.config.isSpecialFn === "string") {
      return Boolean(item[this.config.isSpecialFn]);
    }
    return false;
  }

  private createRedPacket(): RedPacket {
    const redPacketData = this.getRedPacketData();
    const baseRedPacket: RedPacket = {
      id: Math.random().toString(36).slice(2, 11),
      x: 0,
      y: 0,
      width: this.config.size.width,
      height: this.config.size.height,
      speed: 0,
      rotation: 0,
      rotationSpeed: 0,
      scale: 1,
      opacity: 1,
      collected: false,
      value: Math.floor(Math.random() * 100) + 1,
      type: "normal",
      originalData: null,
    };

    if (redPacketData && this.isUsingArrayMode()) {
      // üéØ Êï∞ÁªÑÊ®°ÂºèÔºö‰ΩøÁî®Êï∞ÁªÑÊï∞ÊçÆÔºåÂèØ‰ª•ÊúâÁâπÊÆäÁ∫¢ÂåÖ
      baseRedPacket.id = redPacketData.id || baseRedPacket.id;
      baseRedPacket.value = redPacketData.value || baseRedPacket.value;
      baseRedPacket.type = this.isSpecialRedPacket(redPacketData) ? "special" : "normal";
      baseRedPacket.originalData = redPacketData; // ‰øùÂ≠òÂéüÂßãÊï∞ÊçÆ
    } else {
      // üéØ ÈöèÊú∫Ê®°ÂºèÔºöÂè™ÁîüÊàêÊôÆÈÄöÁ∫¢ÂåÖÔºå‰∏ç‰ΩøÁî®ÁâπÊÆäÂà§Êñ≠
      baseRedPacket.type = "normal";
      baseRedPacket.originalData = null;
    }

    return baseRedPacket;
  }

  private resetRedPacket(redPacket: RedPacket): void {
    const redPacketData = this.getRedPacketData();

    // ‰ΩçÁΩÆÂíåÁâ©ÁêÜÂ±ûÊÄß
    redPacket.x = Math.random() * (this.actualWidth - this.config.size.width);
    redPacket.y = -this.config.size.height;
    redPacket.speed =
      Number(this.config.speed.min) + Math.random() * (Number(this.config.speed.max) - Number(this.config.speed.min));
    redPacket.rotation = 0;
    redPacket.rotationSpeed = this.config.rotation
      ? this.config.rotationSpeed.min + Math.random() * (this.config.rotationSpeed.max - this.config.rotationSpeed.min)
      : 0;
    redPacket.scale = 0.8 + Math.random() * 0.4;
    redPacket.opacity = 1;
    redPacket.collected = false;

    // Êï∞ÊçÆÂ±ûÊÄß
    if (redPacketData && this.isUsingArrayMode()) {
      // Êï∞ÁªÑÊ®°ÂºèÔºö‰ΩøÁî®Êï∞ÁªÑÊï∞ÊçÆÔºåÂèØ‰ª•ÊúâÁâπÊÆäÁ∫¢ÂåÖ
      redPacket.id = redPacketData.id || redPacket.id;
      redPacket.value = redPacketData.value || Math.floor(Math.random() * 100) + 1;
      redPacket.type = this.isSpecialRedPacket(redPacketData) ? "special" : "normal";
      redPacket.originalData = redPacketData; // ‰øùÂ≠òÂéüÂßãÊï∞ÊçÆ
    } else {
      // ÈöèÊú∫Ê®°ÂºèÔºöÂè™ÁîüÊàêÊôÆÈÄöÁ∫¢ÂåÖ
      redPacket.value = Math.floor(Math.random() * 100) + 1;
      redPacket.type = "normal"; // Âº∫Âà∂‰∏∫ÊôÆÈÄöÁ∫¢ÂåÖ
      redPacket.originalData = null; // Ê∏ÖÁ©∫ÂéüÂßãÊï∞ÊçÆ
    }
  }

  private optimizeConfig(): void {
    //  redPackets„ÄÅcount ÂíåÈªòËÆ§ÂÄºÁöÑÂ§ÑÁêÜÈÄªËæë
    if (this.config.redPackets && Array.isArray(this.config.redPackets) && this.config.redPackets.length > 0) {
      // ‰ºòÂÖàÁ∫ß1: Â¶ÇÊûú‰º†‰∫ÜÊúâÊïàÊï∞ÁªÑÔºå‰ΩøÁî®Êï∞ÁªÑÈïøÂ∫¶‰Ωú‰∏∫ count
      this.config.count = this.config.redPackets.length;
    } else if (this.config.count === undefined || this.config.count === null || this.config.count <= 0) {
      // ‰ºòÂÖàÁ∫ß3: Â¶ÇÊûúÊ≤°‰º†ÊúâÊïàÁöÑ countÔºå‰ΩøÁî®ÈªòËÆ§ÂÄº
      this.config.count = 20;
    }
  }

  // üéØ Âà§Êñ≠ÂΩìÂâçÊòØÂê¶‰ΩøÁî®Êï∞ÁªÑÊ®°Âºè
  private isUsingArrayMode(): boolean {
    return !!(this.config.redPackets && Array.isArray(this.config.redPackets) && this.config.redPackets.length > 0);
  }

  private loadRedPacketImage(): void {
    if (this.config.redPacketImage) {
      if (typeof this.config.redPacketImage === "string") {
        this.redPacketImage = new Image();
        this.redPacketImage.src = this.config.redPacketImage;
      } else {
        this.redPacketImage = this.config.redPacketImage;
      }
    }
  }

  private resizeCanvas(): void {
    let width = 800,
      height = 600;

    if (typeof this.config.containerWidth === "string" && this.config.containerWidth.includes("%")) {
      const parent = this.canvas.parentElement;
      if (parent) {
        width = (parent.clientWidth * parseFloat(this.config.containerWidth)) / 100;
      }
    } else {
      width = typeof this.config.containerWidth === "number" ? this.config.containerWidth : 800;
    }

    if (typeof this.config.containerHeight === "string" && this.config.containerHeight.includes("%")) {
      const parent = this.canvas.parentElement;
      if (parent) {
        height = (parent.clientHeight * parseFloat(this.config.containerHeight)) / 100;
      }
    } else {
      height = typeof this.config.containerHeight === "number" ? this.config.containerHeight : 600;
    }

    this.canvas.width = this.actualWidth = Math.max(100, Math.min(width, 4000));
    this.canvas.height = this.actualHeight = Math.max(100, Math.min(height, 4000));
  }

  private bindEvents(): void {
    this.canvas.addEventListener("click", this.handleClick.bind(this));
    this.canvas.addEventListener("touchstart", this.handleTouch.bind(this));
    window.addEventListener("resize", this.handleResize.bind(this));
  }

  private handleClick(event: MouseEvent): void {
    const coords = this.getCanvasCoordinates(event.clientX, event.clientY);
    this.checkCollision(coords.x, coords.y, "mouse");
  }

  private handleTouch(event: TouchEvent): void {
    event.preventDefault();
    const touch = event.touches[0];
    const coords = this.getCanvasCoordinates(touch.clientX, touch.clientY);
    this.checkCollision(coords.x, coords.y, "touch");
  }

  // üéØ ‰ºòÂåñÂùêÊ†áËΩ¨Êç¢ÔºåËÄÉËôëCanvasÁº©ÊîæÂíåËÆæÂ§áÂÉèÁ¥†ÊØî
  private getCanvasCoordinates(clientX: number, clientY: number): { x: number; y: number } {
    const rect = this.canvas.getBoundingClientRect();

    // ËÆ°ÁÆóCanvasÁöÑÂÆûÈôÖÊòæÁ§∫Â∞∫ÂØ∏
    const displayWidth = rect.width;
    const displayHeight = rect.height;

    // ËÆ°ÁÆóÁº©ÊîæÊØî‰æã
    const scaleX = this.canvas.width / displayWidth;
    const scaleY = this.canvas.height / displayHeight;

    // ËΩ¨Êç¢ÂùêÊ†á
    const x = (clientX - rect.left) * scaleX;
    const y = (clientY - rect.top) * scaleY;

    if (this.config.debugMode) {
      console.log(`üéØ ÁÇπÂáªÂùêÊ†áËΩ¨Êç¢:
        ÂÆ¢Êà∑Á´ØÂùêÊ†á: (${clientX}, ${clientY})
        CanvasÁü©ÂΩ¢: ${JSON.stringify(rect)}
        Áº©ÊîæÊØî‰æã: (${scaleX.toFixed(3)}, ${scaleY.toFixed(3)})
        ÊúÄÁªàÂùêÊ†á: (${x.toFixed(1)}, ${y.toFixed(1)})`);
    }

    return { x, y };
  }

  private handleResize(): void {
    this.resizeCanvas();
  }

  private checkCollision(x: number, y: number, inputType: string = "mouse"): void {
    const currentTime = Date.now();

    // üéØ Èò≤ÊäñÂ§ÑÁêÜ - Èò≤Ê≠¢Âø´ÈÄüÈáçÂ§çÁÇπÂáª
    if (currentTime - this.lastClickTime < this.clickDebounceDelay) {
      if (this.config.debugMode) {
        console.log(`üéØ ÁÇπÂáªË¢´Èò≤ÊäñËøáÊª§ (Èó¥Èöî: ${currentTime - this.lastClickTime}ms)`);
      }
      return;
    }
    this.lastClickTime = currentTime;

    if (this.config.debugMode) {
      console.log(`üéØ Ê£ÄÊü•Á¢∞Êíû: (${x.toFixed(1)}, ${y.toFixed(1)}) [${inputType}]`);
    }

    let hitFound = false;
    for (let i = this.redPackets.length - 1; i >= 0; i--) {
      const redPacket = this.redPackets[i];
      if (this.isPointInRedPacket(x, y, redPacket)) {
        if (this.config.debugMode) {
          console.log(`üéØ ÂëΩ‰∏≠Á∫¢ÂåÖ: ${redPacket.id}, Á±ªÂûã: ${redPacket.type}, ‰ª∑ÂÄº: ${redPacket.value}`);
        }
        this.collectRedPacket(redPacket, x, y);
        this.redPackets.splice(i, 1);
        this.returnRedPacketToPool(redPacket);
        hitFound = true;
        break;
      }
    }

    // üéØ ËÆ∞ÂΩïÁÇπÂáªË∞ÉËØï‰ø°ÊÅØ
    if (this.config.debugMode) {
      this.debugClicks.push({ x, y, time: currentTime, hit: hitFound });

      if (!hitFound) {
        console.log(`üéØ Êú™ÂëΩ‰∏≠‰ªª‰ΩïÁ∫¢ÂåÖ - ÂΩìÂâçÁ∫¢ÂåÖÊï∞Èáè: ${this.redPackets.length}`);
        // ÊòæÁ§∫ÊúÄËøëÁöÑÁ∫¢ÂåÖ‰ΩçÁΩÆ
        this.redPackets.slice(0, 3).forEach((rp, idx) => {
          const centerX = rp.x + rp.width / 2;
          const centerY = rp.y + rp.height / 2;
          const distance = Math.sqrt((x - centerX) ** 2 + (y - centerY) ** 2);
          console.log(
            `Á∫¢ÂåÖ${idx + 1}: ‰∏≠ÂøÉ(${centerX.toFixed(1)}, ${centerY.toFixed(1)}), Ë∑ùÁ¶ª: ${distance.toFixed(1)}`
          );
        });
      }
    }
  }

  // Âà§Êñ≠ÁÇπÊòØÂê¶Âú®Á∫¢ÂåÖÂÜÖ
  private isPointInRedPacket(x: number, y: number, redPacket: RedPacket): boolean {
    const centerX = redPacket.x + redPacket.width / 2;
    const centerY = redPacket.y + redPacket.height / 2;
    const width = redPacket.width * redPacket.scale;
    const height = redPacket.height * redPacket.scale;

    // üéØ Â¢ûÂä†ÂÆπÈîôËåÉÂõ¥ÔºåËÆ©Á∫¢ÂåÖÊõ¥ÂÆπÊòìÁÇπÂáªÔºàÂ¢ûÂä†10%ÁöÑËæπÁïåÔºâ
    const toleranceX = width * 0.1;
    const toleranceY = height * 0.1;
    const expandedWidth = width + toleranceX;
    const expandedHeight = height + toleranceY;

    const isInBounds =
      x >= centerX - expandedWidth / 2 &&
      x <= centerX + expandedWidth / 2 &&
      y >= centerY - expandedHeight / 2 &&
      y <= centerY + expandedHeight / 2;

    if (this.config.debugMode && isInBounds) {
      console.log(`üéØ Á∫¢ÂåÖËæπÁïåÊ£ÄÊµã:
        Á∫¢ÂåÖ‰∏≠ÂøÉ: (${centerX.toFixed(1)}, ${centerY.toFixed(1)})
        ÂéüÂßãÂ∞∫ÂØ∏: ${width.toFixed(1)} x ${height.toFixed(1)}
        Êâ©Â±ïÂ∞∫ÂØ∏: ${expandedWidth.toFixed(1)} x ${expandedHeight.toFixed(1)}
        ÁÇπÂáªÂùêÊ†á: (${x.toFixed(1)}, ${y.toFixed(1)})
        Ë∑ùÁ¶ª‰∏≠ÂøÉ: (${Math.abs(x - centerX).toFixed(1)}, ${Math.abs(y - centerY).toFixed(1)})`);
    }

    return isInBounds;
  }

  // Êî∂ÈõÜÁ∫¢ÂåÖ
  private collectRedPacket(redPacket: RedPacket, x: number, y: number): void {
    redPacket.collected = true;

    this.stats.totalCollected++;
    this.stats.totalValue += redPacket.value;

    // Á∫¢ÂåÖÁ≤íÂ≠êÊïàÊûú
    if (!this.config.enablePerformanceMode && this.config.isParticle) {
      this.createRedPacketParticles(redPacket, x, y);
    }

    if (this.onRedPacketClickCallback) {
      this.onRedPacketClickCallback({
        redPacket,
        x,
        y,
        value: redPacket.value,
      });
    }
  }

  // üéÜ ÂàõÂª∫ÁÇπÂáª‰πãÂêéÁ∫¢ÂåÖÁ≤íÂ≠êÊïàÊûú
  private createRedPacketParticles(redPacket: RedPacket, x: number, y: number): void {
    const particleCount = this.particleConfig.count;

    // ÁâπÊÆäÁ∫¢ÂåÖ‰∫ßÁîüÊõ¥Â§öÁ≤íÂ≠ê
    const actualCount = redPacket.type === "special" ? particleCount * 1.5 : particleCount;

    for (let i = 0; i < actualCount; i++) {
      const particle = this.getParticleFromPool();
      this.resetParticle(particle, x, y);

      // ÁâπÊÆäÁ∫¢ÂåÖÁöÑÁ≤íÂ≠êÊúâÈáëËâ≤ÂÖâËäíÊïàÊûú
      if (redPacket.type === "special") {
        particle.color = "#FFD700"; // ÈáëËâ≤
        particle.size *= 1.3; // Êõ¥Â§ß
      }

      this.particles.push(particle);
    }

    if (this.config.debugMode) {
      console.log(`üéÜ ÂàõÂª∫‰∫Ü ${actualCount} ‰∏™Á≤íÂ≠ê [Á∫¢ÂåÖÁ±ªÂûã: ${redPacket.type}]`);
    }
  }

  // üéÜ Êõ¥Êñ∞Á≤íÂ≠êÁä∂ÊÄÅ
  private updateParticles(deltaTime: number): void {
    const timeStep = deltaTime / 16.67; // Ê†áÂáÜÂåñÂà∞60fps

    for (let i = this.particles.length - 1; i >= 0; i--) {
      const particle = this.particles[i];

      // Êõ¥Êñ∞‰ΩçÁΩÆ
      particle.x += particle.vx * timeStep;
      particle.y += particle.vy * timeStep;

      // Â∫îÁî®ÈáçÂäõ
      particle.vy += particle.gravity * timeStep;

      // Â∫îÁî®Êë©Êì¶Âäõ
      particle.vx *= particle.friction;
      particle.vy *= particle.friction;

      // Êõ¥Êñ∞ÊóãËΩ¨
      particle.rotation += particle.rotationSpeed * timeStep;

      // Êõ¥Êñ∞ÁîüÂëΩÂÄº
      particle.life -= deltaTime;

      // Ê†πÊçÆÁîüÂëΩÂÄºÊõ¥Êñ∞ÈÄèÊòéÂ∫¶ÂíåÂ§ßÂ∞è
      const lifeRatio = Math.max(0, particle.life / particle.maxLife);
      particle.opacity = lifeRatio;

      // Á≤íÂ≠êÂú®ÁîüÂëΩÊú´Êúü‰ºöÂèòÂ∞è - ‰øùÂ≠òÂàùÂßãÂ§ßÂ∞èÂπ∂Â∫îÁî®Áº©Êîæ
      if (!particle.initialSize) {
        particle.initialSize = particle.size;
      }
      const sizeRatio = 0.3 + lifeRatio * 0.7; // ÊúÄÂ∞è30%Â§ßÂ∞è
      particle.size = particle.initialSize * sizeRatio;

      // ÁßªÈô§Ê≠ª‰∫°ÁöÑÁ≤íÂ≠ê
      if (particle.life <= 0 || particle.y > this.actualHeight + 50) {
        this.particles.splice(i, 1);
        this.returnParticleToPool(particle);
      }
    }
  }

  // üéÜ Ê∏≤ÊüìÁ≤íÂ≠ê
  private renderParticles(): void {
    const ctx = this.ctx;

    for (const particle of this.particles) {
      ctx.save();

      ctx.globalAlpha = particle.opacity;
      ctx.translate(particle.x, particle.y);
      ctx.rotate(particle.rotation);

      // ÂàõÂª∫Ê∏êÂèòÊïàÊûú
      const gradient = ctx.createRadialGradient(0, 0, 0, 0, 0, particle.size);
      gradient.addColorStop(0, particle.color);
      gradient.addColorStop(0.7, particle.color + "80"); // 50% ÈÄèÊòé
      gradient.addColorStop(1, particle.color + "00"); // ÂÆåÂÖ®ÈÄèÊòé

      ctx.fillStyle = gradient;
      ctx.beginPath();
      ctx.arc(0, 0, particle.size, 0, Math.PI * 2);
      ctx.fill();

      // ‰∏∫ÁâπÊÆäÁ≤íÂ≠êÊ∑ªÂä†ÂèëÂÖâÊïàÊûú
      if (particle.color === "#FFD700" && !this.config.enablePerformanceMode) {
        ctx.shadowColor = particle.color;
        ctx.shadowBlur = particle.size * 2;
        ctx.beginPath();
        ctx.arc(0, 0, particle.size * 0.5, 0, Math.PI * 2);
        ctx.fill();
      }

      ctx.restore();
    }
  }

  private spawnRedPacket(): void {
    const currentTime = Date.now();
    const spawnInterval = 1000 / this.config.density;

    if (currentTime - this.lastSpawnTime > spawnInterval && this.redPackets.length < this.config.count) {
      const redPacket = this.getRedPacketFromPool();
      this.redPackets.push(redPacket);
      this.lastSpawnTime = currentTime;
    }
  }

  private updateRedPackets(deltaTime: number): void {
    for (let i = this.redPackets.length - 1; i >= 0; i--) {
      const redPacket = this.redPackets[i];

      redPacket.y += redPacket.speed * (deltaTime / 16.67);
      if (this.config.rotation) {
        redPacket.rotation += redPacket.rotationSpeed * (deltaTime / 16.67);
      }

      if (redPacket.y > this.actualHeight) {
        this.redPackets.splice(i, 1);
        this.returnRedPacketToPool(redPacket);
      }
    }
  }

  private renderRedPackets(): void {
    this.ctx.clearRect(0, 0, this.actualWidth, this.actualHeight);

    if (this.config.customStyles?.backgroundColor) {
      this.ctx.fillStyle = this.config.customStyles.backgroundColor;
      this.ctx.fillRect(0, 0, this.actualWidth, this.actualHeight);
    }

    for (const redPacket of this.redPackets) {
      this.renderRedPacket(redPacket);
    }
  }

  private renderRedPacket(redPacket: RedPacket): void {
    const ctx = this.ctx;
    const centerX = redPacket.x + redPacket.width / 2;
    const centerY = redPacket.y + redPacket.height / 2;

    ctx.save();
    ctx.globalAlpha = redPacket.opacity;
    ctx.translate(centerX, centerY);

    if (this.config.rotation) {
      ctx.rotate(redPacket.rotation);
    }
    ctx.scale(redPacket.scale, redPacket.scale);

    if (!this.config.enablePerformanceMode && redPacket.type === "special") {
      ctx.shadowColor = "#FFD700";
      ctx.shadowBlur = 10;
    }

    if (this.redPacketImage) {
      ctx.drawImage(
        this.redPacketImage,
        -redPacket.width / 2,
        -redPacket.height / 2,
        redPacket.width,
        redPacket.height
      );
    } else {
      this.drawDefaultRedPacket(ctx, redPacket);
    }

    ctx.restore();
  }

  private drawDefaultRedPacket(ctx: CanvasRenderingContext2D, redPacket: RedPacket): void {
    const width = redPacket.width;
    const height = redPacket.height;

    ctx.fillStyle = redPacket.type === "special" ? "#FFD700" : "#FF6B6B";
    ctx.fillRect(-width / 2, -height / 2, width, height);

    ctx.strokeStyle = this.config.customStyles?.borderColor || "#FF4444";
    ctx.lineWidth = 2;
    ctx.strokeRect(-width / 2, -height / 2, width, height);

    ctx.fillStyle = "#FFFFFF";
    ctx.font = "20px Arial";
    ctx.textAlign = "center";
    ctx.textBaseline = "middle";
    ctx.fillText("Ôø•", 0, -5);

    if (redPacket.value) {
      ctx.font = "10px Arial";
      ctx.fillText(redPacket.value.toString(), 0, 8);
    }
  }

  private animate(currentTime: number): void {
    if (!this.isRunning || this.isPaused) return;

    const maxFPS = this.config.enablePerformanceMode ? this.config.maxFPS : this.config.qualityMaxFPS || 144;
    const targetFrameTime = 1000 / maxFPS;
    const deltaTime = currentTime - this.lastFrameTime;

    if (deltaTime < targetFrameTime * 0.9) {
      if (this.isRunning && !this.isPaused) {
        this.animationId = requestAnimationFrame(this.animate.bind(this));
      }
      return;
    }

    this.lastFrameTime = currentTime;
    this.updateFPS(currentTime);
    this.spawnRedPacket();
    this.updateRedPackets(deltaTime);
    this.updateParticles(deltaTime); // üéÜ Êõ¥Êñ∞Á≤íÂ≠ê
    this.renderRedPackets();
    this.renderParticles(); // üéÜ Ê∏≤ÊüìÁ≤íÂ≠ê

    if (this.config.debugMode) {
      this.renderDebugInfo();
    }

    this.triggerCallbacks();

    if (this.isRunning && !this.isPaused) {
      this.animationId = requestAnimationFrame(this.animate.bind(this));
    }
  }

  private updateFPS(currentTime: number): void {
    this.frameCount++;
    if (currentTime - this.lastFpsTime >= 1000) {
      this.stats.fps = this.frameCount;
      this.frameCount = 0;
      this.lastFpsTime = currentTime;
    }
  }

  private renderDebugInfo(): void {
    const ctx = this.ctx;
    ctx.save();

    // üéØ Ë∞ÉËØï‰ø°ÊÅØÈù¢Êùø
    ctx.fillStyle = "rgba(0, 0, 0, 0.8)";
    ctx.fillRect(10, 10, 300, 120);
    ctx.fillStyle = "#00FF00";
    ctx.font = "14px Arial";
    ctx.textAlign = "left";
    ctx.fillText(`FPS: ${this.stats.fps}`, 15, 30);
    ctx.fillText(`Á∫¢ÂåÖÊï∞Èáè: ${this.redPackets.length}`, 15, 50);
    ctx.fillText(`Á≤íÂ≠êÊï∞Èáè: ${this.particles.length}`, 15, 70); // üéÜ Á≤íÂ≠êÊï∞Èáè
    ctx.fillText(`CanvasÂ∞∫ÂØ∏: ${this.actualWidth}x${this.actualHeight}`, 15, 90);
    ctx.fillText(`ÁÇπÂáªËÆ∞ÂΩï: ${this.debugClicks.length}`, 15, 110);

    // üéØ ÁªòÂà∂Á∫¢ÂåÖËæπÁïåÂíå‰∏≠ÂøÉÁÇπ
    for (const redPacket of this.redPackets) {
      const centerX = redPacket.x + redPacket.width / 2;
      const centerY = redPacket.y + redPacket.height / 2;
      const width = redPacket.width * redPacket.scale;
      const height = redPacket.height * redPacket.scale;

      // Êâ©Â±ïÁöÑÁÇπÂáªÂå∫ÂüüÔºà10%ÂÆπÈîôÔºâ
      const toleranceX = width * 0.1;
      const toleranceY = height * 0.1;
      const expandedWidth = width + toleranceX;
      const expandedHeight = height + toleranceY;

      // ÁªòÂà∂ÂéüÂßãËæπÁïåÔºàÁ∫¢Ëâ≤Ôºâ
      ctx.strokeStyle = "rgba(255, 0, 0, 0.5)";
      ctx.lineWidth = 1;
      ctx.strokeRect(centerX - width / 2, centerY - height / 2, width, height);

      // ÁªòÂà∂Êâ©Â±ïËæπÁïåÔºàÁªøËâ≤Ôºâ
      ctx.strokeStyle = "rgba(0, 255, 0, 0.7)";
      ctx.lineWidth = 2;
      ctx.strokeRect(centerX - expandedWidth / 2, centerY - expandedHeight / 2, expandedWidth, expandedHeight);

      // ÁªòÂà∂‰∏≠ÂøÉÁÇπ
      ctx.fillStyle = "rgba(0, 255, 255, 0.8)";
      ctx.fillRect(centerX - 2, centerY - 2, 4, 4);
    }

    // üéØ ÁªòÂà∂ÊúÄËøëÁöÑÁÇπÂáª‰ΩçÁΩÆ
    const currentTime = Date.now();
    for (const click of this.debugClicks) {
      const age = currentTime - click.time;
      if (age < 2000) {
        // ÊòæÁ§∫ÊúÄËøë2ÁßíÁöÑÁÇπÂáª
        const alpha = Math.max(0, 1 - age / 2000);
        ctx.fillStyle = click.hit ? `rgba(0, 255, 0, ${alpha})` : `rgba(255, 0, 0, ${alpha})`;
        ctx.fillRect(click.x - 5, click.y - 5, 10, 10);

        // ÁÇπÂáªÂúÜÂúà
        ctx.strokeStyle = click.hit ? `rgba(0, 255, 0, ${alpha})` : `rgba(255, 0, 0, ${alpha})`;
        ctx.lineWidth = 2;
        ctx.beginPath();
        ctx.arc(click.x, click.y, 15, 0, Math.PI * 2);
        ctx.stroke();
      }
    }

    ctx.restore();
  }

  private triggerCallbacks(): void {
    if (this.onAnimationStateChangeCallback) {
      this.onAnimationStateChangeCallback({
        isRunning: this.isRunning,
        isPaused: this.isPaused,
        totalCollected: this.stats.totalCollected,
        currentRedPackets: [...this.redPackets],
        fps: this.stats.fps,
      });
    }

    if (this.onPerformanceUpdateCallback) {
      this.onPerformanceUpdateCallback({
        fps: this.stats.fps,
        renderTime: 0,
        updateTime: 0,
        totalRedPackets: this.stats.totalCollected,
        activeRedPackets: this.redPackets.length,
      });
    }
  }

  // ÂºÄÂßãÂä®Áîª
  public start(): void {
    if (!this.isRunning) {
      this.isRunning = true;
      this.isPaused = false;
      this.lastFrameTime = performance.now();
      this.animate(this.lastFrameTime);
    }
  }
  // ÂÅúÊ≠¢Âä®Áîª
  public stop(): void {
    this.isRunning = false;
    if (this.animationId) {
      cancelAnimationFrame(this.animationId);
      this.animationId = null;
    }
  }
  // ÊöÇÂÅúÂä®Áîª
  public pause(): void {
    this.isPaused = true;
  }
  // ÊÅ¢Â§çÂä®Áîª
  public resume(): void {
    if (this.isPaused && this.isRunning) {
      this.isPaused = false;
      this.lastFrameTime = performance.now();
      this.animate(this.lastFrameTime);
    }
  }
  // Ê∏ÖÈô§Á∫¢ÂåÖ
  public clear(): void {
    this.redPackets.forEach((redPacket) => this.returnRedPacketToPool(redPacket));
    this.redPackets = [];

    // üéÜ Ê∏ÖÈô§Á≤íÂ≠ê
    this.particles.forEach((particle) => this.returnParticleToPool(particle));
    this.particles = [];

    this.ctx.clearRect(0, 0, this.actualWidth, this.actualHeight);
    this.stats.totalCollected = 0;
    this.stats.totalValue = 0;
  }

  // Êõ¥Êñ∞ÈÖçÁΩÆ
  public updateConfig(newConfig: Partial<RedPacketRainConfig>): void {
    this.config = { ...this.config, ...newConfig };

    // üéØ ‰ºòÂåñÈÖçÁΩÆÂ§ÑÁêÜÈÄªËæë
    this.optimizeConfig();

    this.resizeCanvas();
    if (newConfig.redPacketImage) {
      this.loadRedPacketImage();
    }
    if (newConfig.redPackets) {
      this.redPacketDataIndex = 0;
    }
  }
  // Ëé∑ÂèñÁªüËÆ°‰ø°ÊÅØ
  public getStats(): PerformanceStats {
    return {
      fps: this.stats.fps,
      renderTime: 0,
      updateTime: 0,
      totalRedPackets: this.stats.totalCollected,
      activeRedPackets: this.redPackets.length,
    };
  }

  // Ëé∑ÂèñÊî∂ÈõÜÁªüËÆ°
  public getCollectionStats(): { totalCollected: number; totalValue: number } {
    return {
      totalCollected: this.stats.totalCollected,
      totalValue: this.stats.totalValue,
    };
  }

  // ËÆæÁΩÆÊÄßËÉΩÊ®°Âºè
  public setPerformanceMode(enabled: boolean): void {
    this.config.enablePerformanceMode = enabled;
  }

  // Ëé∑ÂèñÊÄßËÉΩÊ®°Âºè
  public getPerformanceMode(): boolean {
    return this.config.enablePerformanceMode;
  }

  // ËÆæÁΩÆÁÇπÂáª‰∫ã‰ª∂ÂõûË∞É
  public onRedPacketClick(callback: RedPacketEventCallback): void {
    this.onRedPacketClickCallback = callback;
  }

  // ËÆæÁΩÆÂä®ÁîªÁä∂ÊÄÅÊîπÂèòÂõûË∞É
  public onAnimationStateChange(callback: AnimationStateCallback): void {
    this.onAnimationStateChangeCallback = callback;
  }

  // ËÆæÁΩÆÊÄßËÉΩÊõ¥Êñ∞ÂõûË∞É
  public onPerformanceUpdate(callback: PerformanceCallback): void {
    this.onPerformanceUpdateCallback = callback;
  }

  // üéÜ ËÆæÁΩÆÁ≤íÂ≠êÊïàÊûúÈÖçÁΩÆ
  public setParticleConfig(config: Partial<ParticleConfig>): void {
    this.particleConfig = { ...this.particleConfig, ...config };
  }

  // üéÜ Ëé∑ÂèñÁ≤íÂ≠êÊïàÊûúÈÖçÁΩÆ
  public getParticleConfig(): ParticleConfig {
    return { ...this.particleConfig };
  }

  // ÈîÄÊØÅÂºïÊìé
  public destroy(): void {
    this.stop();
    this.clear();

    // üéØ Ê∏ÖÁêÜË∞ÉËØïÊï∞ÊçÆ
    this.debugClicks = [];
    this.lastClickTime = 0;

    // üéÜ Ê∏ÖÁêÜÁ≤íÂ≠êÊ±†
    this.particlePool = [];

    this.canvas.removeEventListener("click", this.handleClick.bind(this));
    this.canvas.removeEventListener("touchstart", this.handleTouch.bind(this));
    window.removeEventListener("resize", this.handleResize.bind(this));
  }
}
